@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@300&family=Raleway:wght@100&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

@-webkit-keyframes spin {
  from { border: 5px solid #ca094d }
  to {   border: 1px solid #ca094d;}
}

@keyframes spin {
  from { border: 5px solid #ca094d }
  to {   border: 1px solid #ca094d;}
}


code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
  font-family: 'math';
  src: local('math'), url(/Math-Editor/static/media/math.45201533.otf) format('opentype');
  /* other formats include: 'woff2', 'truetype, 'opentype',
                            'embedded-opentype', and 'svg' */
}

@font-face {
  font-family: 'math2';
  src: local('math2'), url(/Math-Editor/static/media/math2.d42d12cd.otf) format('opentype');
  /* other formats include: 'woff2', 'truetype, 'opentype',
                            'embedded-opentype', and 'svg' */
}



.mfrac {
  font-size: 30px;
}

.comment {
  color: grey !important;
}

.comment > .normal {
  color: grey !important;
}

*:focus {
  outline: #3f51b5 !important;
  /* border:1px solid red; */
}

.center {
  text-align: center;
  width: 50%;
  margin: 0 auto;
}
  

::-webkit-scrollbar {
  width: 10px;
  /* height: 100px;               */
}

::-webkit-scrollbar-track {
  background: #f1f1f1; 
}
 
::-webkit-scrollbar-thumb {
  background: #c1c1c1; 
}

::-webkit-scrollbar-thumb:hover {
  background: #3f51b5; 
}

textarea {
  
  font-family:Georgia, 'Times New Roman', Times, serif;
  font-weight: 500;
}


html,
body {
  height: 100% !important;
  margin: 0 !important;
  overflow: hidden;
}



